const sliders = (slideOne, time, slideTwo, next, prev) => {                 // slideOne -   , time -       , slideTwo -   , next -  , prev -                                         

    //  
    const slides = document.querySelectorAll(slideOne);                     //      

    let count = 0;                                                          //    

    function slideHide() {                                                  //    
        slides.forEach(item => {                                            // ..  ,   forEach
            item.classList.add('hide');                                     //     bootstrap.css
            item.classList.remove('show');                                  //   
        });
    }

    function slideShow(i = 0) {                                             //    .  i=0  ,      
        slides[i].classList.add('show', 'animated', 'slideInDown');         //       
        slides[i].classList.remove('hide');                                 //      
    }
    
    slideHide();                                                            //   
    slideShow();                                                            //   

    function slideStep() {                                                  //   
        slideHide();                                                        //   
        count++;                                                            //     +1
        slideShow(count);                                                   //   
    }

    setInterval(() => {                                                     //    
        if (count + 1 >= slides.length) {                                   //      - 
            count = -1;                                                     //    -1, ..     1,     0
        }
        slideStep();                                                        //    
    }, time);                                                               //     
        

    //  
    const nextSlide = document.querySelector(next),                         //   
          prevSlide = document.querySelector(prev),                         //   
          slidesTwo = document.querySelectorAll(slideTwo);                  //      

    let countTwo = 0,                                                       //    
        intervalId = 0;                                                     //      

    function slideHideTwo() {                                               //    
        slidesTwo.forEach(item => {                                         // ..  ,   forEach
            item.classList.add('hide');                                     //     bootstrap.css
            item.classList.remove('show');                                  //   
            item.classList.remove('slideInRight');                          //    
            item.classList.remove('slideInLeft');                           //    
        });
    }

    function slideShowNext(i = 0) {                                         //      next.  i=0  ,      
        slidesTwo[i].classList.add('show', 'animated', 'slideInRight');     //       
        slidesTwo[i].classList.remove('hide');                              //      
    }

    function slideShowPrev(i) {                                             //      prev
        slidesTwo[i].classList.add('show', 'animated', 'slideInLeft');      //       
        slidesTwo[i].classList.remove('hide');                              //      
    }

    slideHideTwo();                                                         //   
    slideShowNext();                                                        //   

    function slideStepTwo() {                                               //    
        slideHideTwo();                                                     //   
        countTwo++;                                                         //     +1
        slideShowNext(countTwo);                                            //   
    }

    function interval() {                                                   //       
        intervalId = setInterval(() => {                                    //     setInterval
            if (countTwo + 1 >= slidesTwo.length) {                         //      - 
                countTwo = -1;                                              //    -1, ..     1,     0
            }
            slideStepTwo();                                                 //    
        }, time);

        return intervalId;                                                  //  id  setInerval   
    }
    
    intervalId = interval();                                                //  

    nextSlide.addEventListener('click', () => {                             //      
        if (countTwo + 1 >= slidesTwo.length) {                             //      - 
            countTwo = -1;                                                  //    -1, ..     1,     0
        }
        slideStepTwo();                                                     //  

        clearInterval(intervalId);                                          //  

        setTimeout(() => {                                                  //      
            intervalId = interval();                                        //  
        }, time / time);                                                    //        ,   1
    });

    prevSlide.addEventListener('click', () => {                             //      
        countTwo--;                                                         //    1
        if (countTwo + 1 == 0) {                                            //      
            countTwo = slidesTwo.length -1 ;                                //     
        }
        slideHideTwo();                                                     //   
        
        slideShowPrev(countTwo);                                            //  

        clearInterval(intervalId);                                          //  

        setTimeout(() => {                                                  //      
            intervalId = interval();                                        //  
        }, time / time);                                                    //        ,   1
    });
};

export default sliders;