const modals = () => {
    let clickButton = false; 
    function bindModal(triggerSelector, modalSelector, closeSelector, closeClickOverlay = true) { // trigger -   , modal -  , close -   , closeClickOverlay -       
        const trigger = document.querySelectorAll(triggerSelector), //    
              modal = document.querySelector(modalSelector),        //   
              close = document.querySelector(closeSelector),        //   
              windows = document.querySelectorAll('[data-modal]'),  //       

              scroll = calcScroll(),                                //    
              htmlFontSize = calcGift(),                            //   font-size html .  ..     px   rem,           px
              giftElem = document.querySelector('.fixed-gift'),     //    
              rightGift = +getComputedStyle(giftElem).right.slice(0, 2); //     Y   

        trigger.forEach(item => {                                   // ..  ,            
            item.addEventListener('click', (e) => {                 //     
                if (e.target) {                                     //         e.target (    )
                    e.preventDefault();                             //    
                }

                clickButton = true;                                 //       true,   ,       

                if (item == giftElem) {                             //      , 
                    giftElem.style.display = 'none';                //   
                }

                windows.forEach(item => {                           //    
                    item.style.display = 'none';                    //  
                });

                modal.style.display = 'block';                      //   
                document.body.style.overflow = 'hidden';            //          
                document.body.style.marginRight = `${scroll}px`;    //        ,        
                document.querySelector('.fixed-gift').style.right = `${(scroll + rightGift)/htmlFontSize}rem`; //      ,        
            });
        });

        close.addEventListener('click', () => {                     //      
            windows.forEach(item => {                               //    
                item.style.display = 'none';                        //  
            });

            modal.style.display = 'none';                           //   
            document.body.style.overflow = '';                      //       
            document.body.style.marginRight = `0px`;                //        ,        
            let rightGiftOpen = +getComputedStyle(giftElem).right.slice(0, 2);
            document.querySelector('.fixed-gift').style.right = `${(rightGiftOpen - scroll)/htmlFontSize}rem`;
        }); 

        modal.addEventListener('click', (e) => {                    //      
            if (e.target === modal && closeClickOverlay) {          //        ,     (   )   closeClickOverlay = true
                windows.forEach(item => {                           //    
                    item.style.display = 'none';                    //  
                });

                modal.style.display = 'none';                       //   
                document.body.style.overflow = '';                  //       
                document.body.style.marginRight = `0px`;            //        ,        
                let rightGiftOpen = +getComputedStyle(giftElem).right.slice(0, 2);
                document.querySelector('.fixed-gift').style.right = `${(rightGiftOpen - scroll)/htmlFontSize}rem`;
            }
        });
    }

    function showModalByTime(selector, time) {                      //     
        setTimeout(() => {
            let display;                                            //          

            document.querySelectorAll('[data-modal]').forEach(item => {     //    
                if (getComputedStyle(item).display !== 'none') {    //   display      none, ..  
                    display = 'block';                              //   
                }
            });

            if (!display) {
                document.querySelector(selector).style.display = 'block';   //  
                document.body.style.overflow = 'hidden';            //          

                let htmlFontSize = calcGift(),                      //   font-size html .  ..     px   rem
                    giftElem = document.querySelector('.fixed-gift'), //    
                    rightGift = +getComputedStyle(giftElem).right.slice(0, 2), //     Y   
                    scroll = calcScroll();                          //    
                document.body.style.marginRight = `${scroll}px`;    //        ,        
                document.querySelector('.fixed-gift').style.right = `${(scroll + rightGift)/htmlFontSize}rem`; //      ,        
            }
            
        }, time);                                                   //  ,    
    }

    function calcScroll() {                                         //       ,   .        
        let div = document.createElement('div');                    //  

        div.style.width = '50px';                                   //   
        div.style.height = '50px';                                  //   
        div.style.overflowY = 'scroll';                             //     Y
        div.style.visibility = 'hidden';                            //    ,    

        document.body.appendChild(div);                             //    html 
        let scrollWidth = div.offsetWidth - div.clientWidth;        //             
        div.remove();                                               //    

        return scrollWidth;                                         //    
    }

    function calcGift() {                                           //   ,        /   
        let a = document.getElementsByTagName('html'),              //   html 
            calcHtmlFontSize;
        a.forEach(item => {                                         // ..    forEach
            calcHtmlFontSize = +getComputedStyle(item).fontSize.slice(0, 2);  //   font-size html 
        });

        return calcHtmlFontSize;                                    //   font-size
    }

    function endPageModal(modalSelector, giftSelector) {            //    ,            
        const scrollHeight = Math.max(                              //      
            document.body.scrollHeight, document.documentElement.scrollHeight,
            document.body.offsetHeight, document.documentElement.offsetHeight,
            document.body.clientHeight, document.documentElement.clientHeight
        );

        window.addEventListener('scroll', () => {                   //      

            if (!clickButton && window.scrollY >= scrollHeight * 0.97) {    //         95% , 
            
                const modal = document.querySelector(modalSelector),    //   
                      giftElem = document.querySelector(giftSelector);  //    
                giftElem.style.display = 'none';                    //    
                modal.style.display = 'block';                      //   
                clickButton = true;                                 //      true,       1 
                document.body.style.overflow = 'hidden';            //          
                document.body.style.marginRight = `${scroll}px`;    //        ,        
            }
        });
    }

    bindModal('.button-design', '.popup-design', '.popup-design .popup-close');     //      " "
    bindModal('.button-consultation', '.popup-consultation', '.popup-consultation .popup-close');   //      "  "
    bindModal('.fixed-gift', '.popup-gift', '.popup-gift .popup-close');    //      ""

    //showModalByTime('.popup-consultation', 3000);                   //       

    endPageModal('.popup-gift', '.fixed-gift');                     //       
    
};

export default modals;